.main{
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #fff;
    text-align: center;
    padding-top: 2%;
}
.name-heading{
    font-size:30px;
    font-weight: bold;
    letter-spacing: 0.5px;
    color: #0D5DBD;
}
.customized-doctor-heading{
    font-size: 45px;
}
.wrapper{
    display:flex;
    justify-content: right;
    align-items: center;
    padding-top: 5%;
    padding-bottom:10%;
    padding-right:20%;

}

.inside{
    width:25%;
    display:block;
    padding:25px;
    background-color: #fff;
}
.img{
    width:100%;
    background-color: #fff;
    text-align: center;
    font-size:16px;
    font-weight: bold;
    letter-spacing: 0.5px;
    color:#0D5DBD;
    padding-bottom: 10px;
}
.img img{
    width: 150px;
    height: 150px;
}
.img .doctor-qualification{
    color:#0D5DBD;
    font-size: 12px;
    font-weight: bold;
}
.description{
    width:100%;
    font-size: 15px;
    letter-spacing: 0.5px;
    color:#808080;
    text-align: justify;
    text-justify: inter-word;
    background-color:#fff;
    font-weight: 500;
}
.doctor-details-read-more{
    padding:5%;
}
.doctor-details-read-more .read-more-button{
    padding:5%;
    background-color: #0D5DBD;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 0.5px;
    border:2px solid #0D5DBD;
    width:50%;

}

.doctor-details-read-more .read-more-button:hover{
    background-color: #fff;
    color:#0D5DBD;
}
@media screen and (max-width: 992px) {
    .main{
    width: 100% !important;
    }
    .name-heading{
        font-size:20px;
    }
    .customized-doctor-heading{
        font-size: 25px;
    }
    .wrapper{
    padding: 5%;
    justify-content: center;
    }
    .inside{
        width:50%;
        display:block;
    }
    .img img{
        width: 100px;
        height: 100px;
    }
    .doctor-name{
        font-size: 12px;
    }
    .img .doctor-qualification{
        font-size: 10px;
    }
    .description{
        font-size: 10px;
    }
    .doctor-details-read-more .read-more-button{
        font-size: 10px;
    }
}
